import React from "react";
import { Link } from "react-router-dom";

import { ontainerLodo, LogoCO } from "./styles";

import logo from "../../assets/img/Logo.svg";

const Logo = () => {
  return (
    <Link to="/">
      <ontainerLodo>
        <LogoCO src={logo} alt="Logo Company" />
      </ontainerLodo>
    </Link>
  );
};

export default Logo;
